.breadcrumb {
  display: flex;
  margin: 0;
  padding: 0;
  list-style-type: none;

  &__item {
    &:not(:last-of-type)::after {
      content: '|';
      margin-right: 1rem;
      margin-left: 1rem;
      color: $secondary;
    }
  }

  &__link {
    color: $dark;
    font-size: 1.142rem;
    text-decoration: none;

    &--active {
      color: $secondary;
    }
  }
}


.product {
  &__header {
    background-color: transparent;
  }

  &__breadcrumb {
    margin-top: 4.285rem;
  }

  &__row {
    margin-top: 4.285rem;
    margin-bottom: 6.857rem;
    display: flex;
    justify-content: space-between;
    &:last-of-type {
      margin-bottom: 3.571rem;
    }
  }

  &__stars {
    img {
      height: 1.214rem;
    }
  }

  &__col {
    &--left {
      width: 40%;
      max-width: 39.285rem;
    }

    &--right {
      width: 59%;
      max-width: 55.928rem;
      margin-top: -0.285rem;
      .social__link--horizontal:not(:last-of-type) {
        margin-right: 1.214rem;
      }
    }

    &--tab {
      width: 75%;
      max-width: 72.642rem;
    }

    &--view {
      width: 24%;
      max-width: 22.857rem;
    }
  }

  &-name {
    display: flex;
    align-items: baseline;
    padding-bottom: 1.428rem;
    border-bottom: 1px solid #d7d7d7;
  }

  &__title {
    color: $dark;
    font-size: 1.714rem;
    margin: 0;
  }

  &__view {
    margin-left: auto;
    margin-right: 1.214rem;
    color: $secondary;

    &-value {
      font-family: "DIN Pro - Bold";
    }
  }

  &__price {
    color: $dark;
    font-size: 1.714rem;
    margin-top: 0.714rem;

    &-value {
      font-size: 3.428rem;
      margin-right: 0.714rem;
    }

    &-block {
      display: flex;
      justify-content: space-between;
      padding-top: 2.5rem;
      padding-bottom: 2.214rem;
      border-bottom: 1px dashed #d7d7d7;
    }
  }

  &__available {
    display: block;
    margin-top: 0.785rem;
    color: $secondary;
    font-size: 1.142rem;

    &-value {
      color: $dark;
      font-family: "DIN Pro - Bold";
    }
  }

  &__color {
    &-block {
      display: flex;
      padding-top: 2.142rem;
      padding-bottom: 2.142rem;
      align-items: center;
      border-bottom: 1px dashed #d7d7d7;
    }

    &-text {
      display: block;
      color: $dark;
      font-size: 1.142rem;
      margin-right: 2.142rem;
    }
  }

  &__delivery {
    padding-top: 1.928rem;
    padding-bottom: 2.142rem;
    border-bottom: 1px dashed #d7d7d7;

    &-text {
      display: block;
      color: $dark;
      font-size: 1.142rem;
      margin-bottom: 2.142rem;
    }

    &-tab {
      display: flex;
      flex-wrap: wrap;

      .radio:checked+.label__delivery+.product__delivery-block {
        display: block;
      }

      .label__delivery {
        order: -1;
        flex-basis: 10.142rem;
        flex-shrink: 1;
      }

      .product__delivery-block {
        width: 100%;
        display: none;
      }
    }


    &-block {
      padding-top: 1.857rem;
      padding-left: 2.142rem;
      .product__delivery-text {
        margin-bottom: 1.428rem;
      }
    }
  }

  &__buttons {
    display: flex;
    padding-top: 2.142rem;
    padding-bottom: 2.142rem;
    border-bottom: 1px dashed #d7d7d7;
  }

  &__button {
    color: $dark;
    font-size: 1.285rem;
    letter-spacing: 0.18px;
    border: 2px solid #202020;
    padding: 1rem 1.714rem;
    background-color: transparent;
    display: flex;
    align-items: center;

    &--add {
      background-color: $primary;
      color: $light;
      border: 2px solid $primary;
      margin-right: 1.428rem;
      padding: 1rem 1.785rem;
    }

    &--wholesale {
      margin-left: auto;
    }

    &--favorites {
      padding: 1rem 2rem;
    }
  }



  &__bag-img {
    height: 15px;
    margin-right: 1.071rem;
  }

  &__favorites-img {
    height: 1.071rem;
    margin-right: 1.071rem;

    path {
      fill: $dark;
    }
  }

  &__bottom {
    &-block {
      padding-top: 1.785rem;
      display: flex;
      align-items: center;
    }

    &-text {
      color: $dark;

      &--bold {
        font-family: "DIN Pro - Bold";
      }

      &:first-of-type {
        margin-right: 2.142rem;
      }

      /*&:last-of-type {
        margin-right: 1.428rem;
        margin-left: auto;
      }*/
    }
  }




  &__swiper-wrapper {
    &--top {
      width: 100%;
      height: 100%;
    }
  }

  &__slide {
    &--top {
      display: flex;
      justify-content: center;
      align-items: center;
    }

    &--thumbs {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 6.214rem;
      height: 6.214rem;
      border: 1px solid #d7d7d7;
    }
  }
}


.recommended {
  &__title {
    color: $dark;
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    font-size: 2.571rem;
    margin: 0;
    margin-bottom: 2.928rem;
    font-weight: 300;

    &::after {
      content: '';
      display: block;
      height: 1rem;
      background: url('../img/product/right-line-small.svg') no-repeat center / contain;
      width: 23.428rem;
    }

    &::before {
      content: '';
      display: block;
      height: 1rem;
      background: url('../img/product/left-line-small.svg') no-repeat center / contain;
      width: 23.428rem;
    }
  }

  &__slide {
    display: block;
    text-decoration: none;
  }

  &__swiper-container {
    position: relative;
    padding-bottom: 5.642rem;
    padding-left: 0.714rem;
  }

  &__swiper-button-prev,
  &__swiper-button-next {
    width: 3.214rem;
    height: 3.214rem;
    display: flex;
    position: absolute;
    justify-content: center;
    align-items: center;
    border: 1px solid #cccccc;
    bottom: 0;
    top: auto;

    &::after {
      display: none;
    }

    &--active {
      background-color: $dark;
      border-color: $dark;
    }
  }

  &__swiper-button-prev {
    left: 46.3%;
  }

  &__swiper-button-next {
    right: 46.3%;
  }
}


.gallery-top {
  max-width: 39.285rem;
  height: 39.285rem;
  margin: 0;
  margin-bottom: 2.142rem;
  border: 1px solid #d7d7d7;
}


.gallery-thumbs {
  max-width: 39.285rem;
  height: 6.214rem;
}


.label {
  &__color {
    color: $dark;
    display: flex;
    justify-content: flex-end;
    position: relative;
    padding: 0 1.357rem 0 3.285rem;
    align-items: center;
    height: 2.142rem;
    border: 1px solid #d7d7d7;

    &:not(:last-of-type) {
      margin-right: 0.714rem;
    }

    &::before {
      content: '';
      display: block;
      position: absolute;
      width: 1.857rem;
      height: 1.857rem;
      top: 1px;
      left: 1px;
    }

    &--green {
      &::before {
        background-color: #86c255;
      }
    }

    &--yellow {
      &::before {
        background-color: #e9eb5b;
      }
    }

    &--red {
      &::before {
        background-color: #e84b4b;
      }
    }

    &--blue {
      &::before {
        background-color: #4b91e8;
      }
    }
  }

  &__delivery {
    color: $dark;
    font-size: 1.142rem;
    padding-bottom: 1.5rem;
    border-bottom: 1px solid #d7d7d7;
    position: relative;
    padding-left: 2.142rem;


    &::before {
      content: '';
      position: absolute;
      display: block;
      top: 0;
      left: 0;
      width: 1.428rem;
      height: 1.428rem;
      box-shadow: inset 1px 1px 5px rgba(0, 0, 0, 0.2);
      border: 1px solid #cccccc;
      background-color: #ffffff;
      border-radius: 50%;
    }

    &::after {
      position: absolute;
      display: none;
      content: '';
      top: 0.357rem;
      left: 0.357rem;
      width: 0.714rem;
      height: 0.714rem;
      background-color: #202020;
      border-radius: 50%;
    }

    &:first-of-type {
      border-right: 1px solid #d7d7d7;
    }

    &:last-of-type {
      padding-left: 4.285rem;
      flex-grow: 1;

      &::before {
        top: 0;
        left: 2.142rem;
      }

      &::after {
        top: 0.357rem;
        left: 2.5rem;
      }
    }
  }

  &__pickup-point {
    color: $dark;
    line-height: 2.142rem;
    border: 1px solid #d7d7d7;

    &:not(:last-of-type) {
      margin-right: 0.714rem;
    }

    position: relative;
    padding-left: 2.571rem;
    padding-right: 0.714rem;

    &::before {
      content: '';
      position: absolute;
      display: block;
      top: 0.357rem;
      left: 0.357rem;
      width: 1.428rem;
      height: 1.428rem;
      box-shadow: inset 1px 1px 0.357rem rgba(0, 0, 0, 0.2);
      border: 1px solid #cccccc;
      background-color: #ffffff;
      border-radius: 50%;
    }

    &::after {
      position: absolute;
      display: none;
      content: '';
      top: 0.714rem;
      left: 0.714rem;
      width: 0.714rem;
      height: 0.714rem;
      background-color: $dark;
      border-radius: 50%;
    }
  }
}


.radio {
  &__color {
    display: none;

    &:checked+.label__color {
      border-color: $primary;
    }
  }

  &__delivery {
    display: none;

    &:checked+.label__delivery::after {
      display: block;
    }

    &:checked+.label__delivery {
      border-bottom: none;
    }
  }

  &__pickup-point {
    display: none;

    &:checked+.label__pickup-point::after {
      display: block;
    }

    &:checked+.label__pickup-point {
      border-color: $primary;
    }
  }
}


.pickup-points {
  display: flex;
}


.tab {
  display: flex;
  flex-wrap: wrap;

  &__radio {
    display: none;

    &:checked+.label__tab+.tab__content {
      display: block;
    }

    &:checked+.label__tab {
      background-color: #f5f5f5;
    }
  }

  .label__tab {
    order: -1;
    text-align: center;
    padding: 1.142rem 3.214rem 1.214rem;
    color: $dark;
    font-size: 1.285rem;
    letter-spacing: 0.18px;
  }

  &__content {
    width: 100%;
    display: none;
    background-color: #f5f5f5;
    padding: 1.5rem 2.857rem 2rem;

    &-text {
      color: $dark;
      font-size: 1.285rem;
      letter-spacing: 0.18px;
    }
  }
}


.table {
  width: 100%;
  border-collapse: collapse;

  &__row:last-of-type .column {
    border-bottom: none;
  }
}


.column {
  &--left {
    color: $dark;
    font-size: 1.285rem;
    padding: 1.285rem 0 1.071rem;
    letter-spacing: 0.18px;
    border-bottom: 1px solid #e1e1e1;
  }

  &--right {
    color: $dark;
    font-size: 1.285rem;
    font-weight: 700;
    line-height: 3.428rem;
    letter-spacing: 0.18px;
    border-bottom: 1px solid #e1e1e1;
  }
}


.view {
  &__subtitle {
    color: $dark;
    font-size: 1.285rem;
    text-transform: uppercase;
    letter-spacing: 0.18px;
    margin: 0;
  }

  &__card {
    display: flex;
    align-items: start;
    text-decoration: none;

    &-wrap {
      padding-top: 2rem;
      padding-bottom: 1.785rem;

      &:not(:last-of-type) {
        border-bottom: 1px solid #d7d7d7;
      }
    }
  }

  &__img {
    margin-right: 2.142rem;
    margin-top: 0.357rem;

    img {
      width: 100%
    }
  }

  &__description {
    display: flex;
    flex-direction: column;
  }

  &__name {
    color: $dark;
    font-size: 1.142rem;
    line-height: 1.714rem;
    margin-bottom: 1rem;
  }

  &__category {
    display: block;
    color: $secondary;
    font-family: "DIN Pro - Light";
  }

  &__stars {
    margin: 0;
    margin-bottom: 0.428rem;
  }

  &__price {
    font-size: 1.714rem;

    &-tag {
      color: $dark;
      font-size: 1.142rem;
    }
  }
}

.slide-top__img {
  max-width: 16.357rem;
  img {
    width: 100%;
  }
}

.product__slide--thumbs {
  &:first-of-type {
    .slide-thumbs {
      &__img {
        max-width: 3.071rem;
        img {
          width: 100%;
        }
      }
    }
  }
  &:nth-of-type(2) {
    .slide-thumbs {
      &__img {
        max-width: 4.071rem;
        img {
          width: 100%;
        }
      }
    }
  }
  &:nth-of-type(3) {
    .slide-thumbs {
      &__img {
        max-width: 5.5rem;
        img {
          width: 100%;
        }
      }
    }
  }
  &:nth-of-type(4), &:last-of-type {
    .slide-thumbs {
      &__img {
        max-width: 4.357rem;
        img {
          width: 100%;
        }
      }
    }
  }
}

.color-inputs {
  display: flex;
}

.product__bottom-wrap {
  margin-left: auto;
  display: flex;
}

.card__stars.view__stars {
  margin-left: 0;
}

.product__bottom-wrap {
  .social {
    overflow: hidden;
  }
}